<template>
  <div>

      <van-tabs v-model="active" :lazy-render="true" swipeable animated>

        <van-tab title="打卡" style="margin-top: 20px;z-index: 998">
          <div style="height:25px"></div>
          <van-pull-refresh
            v-model="isLoading"
            success-text="刷新成功"
            @refresh="onRefresh"
          >

            <div v-for="(list,index) in newslist">
              <div class="mo">
                <div class="l">
                  <van-image width="100%"
                             height="100%"
                             fit="cover"
                             :src="list.mts_pic"/>
                </div>
                <div class="r">
                  <p class="tit">{{list.mts_content}}</p>
                  <div class="r_b">
                    <p class="time">{{list.mts_time2}}</p>
                    <p class="ava">{{list.mts_user_name}}</p>
                  </div>
                </div>
              </div>
            </div>
          </van-pull-refresh>

        </van-tab>

        <van-tab title="文章">
          <div style="height: 40px"></div>
          <van-pull-refresh v-model="isLoading2" @refresh="onRefresh2" success-text="刷新成功">

            <div v-for="(list,index) in bloglist"
                 style="padding: 5px;margin: 5px;box-sizing: border-box">
              <van-cell style="border: 1px solid rgba(0,0,0,0.3);border-radius: 5px"
                        :title="'◆'+list.news_title"
                        :value="list.news_user_name"
                        is-link
                        @click="toBlogDetails(index)"/>
            </div>
          </van-pull-refresh>
        </van-tab>
      </van-tabs>





  </div>
</template>

<script>

  export default {
    name: "Home",
    data() {
      return {
        // count: 0,
        isLoading: false,
        isLoading2:false,
        newslist: [],
        bloglist: [],
        active: "",
      }
    },
    mounted() {
      const toast = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
        // toast.clear();

        // .catch((err)=>{
        //   toast.clear();
        //
        //   this.$toast.fail({
        //     message: "网络错误，请重试"
        //   });
        // })
      });
      let that = this;
      let URL = "https://zhuzuoji.com/myapi/moments/findallmoments";
      let p = {'key': "vv9544"};
      this.$axios.get(URL, {params: p}).then((suc) => {
        toast.clear();

        // console.log(suc.data);
        that.newslist = suc.data.moments;
      }).catch((err)=>{
            toast.clear();

            this.$toast.fail({
              message: "网络错误，请重试"
            });
          })
      let URL1 = "https://zhuzuoji.com/myapi/blog/findallblog";
      let p1 = {'key': "vv9544"};
      this.$axios.get(URL1, {params: p1}).then((res) => {
        toast.clear();

        // console.log(res.data);
        that.bloglist = res.data.moments;
      }).catch((err)=>{
            toast.clear();

            this.$toast.fail({
              message: "网络错误，请重试"
            });
          })

      let isFromDetails = that.$route.query.is;

      console.log(isFromDetails);
      if (isFromDetails) {
        that.active = 1;
      } else {
        console.log("走")
        that.active = 0;
      }
    },

    methods: {
      toBlogDetails(idx) {
        let that = this;
        let id = this.bloglist[idx].news_id;
        this.$router.push({
          path: '/blogdetails',
          query: {
            blogid: id,
            isFromWhere: "home",
          }
        })
      },
      onRefresh() {
        let that = this;
        let URL = "https://zhuzuoji.com/myapi/moments/findallmoments";
        let p = {'key': "vv9544"};
        this.$axios.get(URL, {params: p}).then((suc) => {
          // console.log(suc.data);
          that.newslist = suc.data.moments;
          this.$toast('刷新成功');
            this.isLoading = false;
        })
      },
      onRefresh2() {
        let that = this;

        let URL1 = "https://zhuzuoji.com/myapi/blog/findallblog";
        let p1 = {'key': "vv9544"};
        this.$axios.get(URL1, {params: p1}).then((res) => {
          // console.log(res.data);
          that.bloglist = res.data.moments;
          this.$toast('刷新成功');
          this.isLoading2 = false;
        })
      }
    },
    destroyed() {
      this.$route.query.is='';
    }
  }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  .mo {
    width: 98%;
    height: 80px;
    /*background: gray;*/
    /*border: greenyellow 1px solid;*/
    padding: 0 5px;
    box-sizing: border-box;
    margin: 5px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }

  .l {
    width: 33%;
    height: 75px;
    float: left;
  }

  .r {
    float: left;
    width: 66%;
    height: 80px;
    padding-left: 2%;
    padding-top: 2%;
    box-sizing: border-box;
  }

  .tit {
    width: 100%;
    height: 60%;
    /*background: red;*/
    overflow:hidden;
  }

  .r_b {
    padding-bottom: 1%;
    box-sizing: border-box;
    width: 100%;
    height: 32%;
    /*background: greenyellow;*/
  }

  .time {

    float: left;
    line-height: 300%;
    font-size: 12px;
    color: gray;
  }

  .ava {
    float: right;
    font-size: 15px;
    line-height: 200%;
    color: gray;
  }
  >>>.van-tabs__wrap{
    z-index: 999;
    position: fixed;
    width: 100%;
    top: 5.6%;
  }
  /*>>>.van-tabs__content{*/
    /*position: ;*/
    /*width: 100%;*/
    /*top: 9%;*/

  /*}*/
  >>>.van-cell__title{
    padding-left: 10px;
  }
  >>>.van-cell__value{
    padding-right: 10px;
  }
</style>



