<template>
  <div>
    <van-nav-bar
      title="详情"
      left-text="我的"
      left-arrow
      @click-left="onClickLeft"
    />
    <h2 class="h2">{{details.news_title}}</h2>
    <div class="msg">
      <p class="ava">{{details.news_user_name}}</p>
      <p class="time">{{(details.news_time2||"").slice(0,10)}}</p>
      <p class="view">浏览：{{details.news_view}}人</p>

    </div>
    <div class="blogdetails_cont" v-html="details.news_content">
    </div>
  </div>
</template>

<script>
  export default {
    name: "Blogdetails",
    data() {
      return {
        details: {},
        isFromWhere:""
      }
    },
    methods: {
      onClickLeft() {
        console.log(this.isFromWhere)
        if(this.isFromWhere=="mine"){
          this.$router.push({
            path: '/Myblogs',

          })
        }else if(this.isFromWhere=="home"){
          this.$router.push({
            path: '/',
            query: {
              is:"home",
              tabBarIndex:0
            }
          })

        }

      },
    },
    mounted() {
      const toast = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
        // toast.clear();

        // .catch((err)=>{
        //   toast.clear();
        //
        //   this.$toast.fail({
        //     message: "网络错误，请重试"
        //   });
        // })
      });
      let that = this;
      let blogIdx = this.$route.query.blogid;
      this.isFromWhere = this.$route.query.isFromWhere;

      // console.log(blogIdx);
      // if(blogIdx){
      //   if(typeof blogIdx != 'number'){ blogIdx = Number(blogIdx) }
      //   console.log(blogIdx);
      // }

      let URL = "https://zhuzuoji.com/myapi/blog/findblogbyblogid";
      let p = {'key': "vv9544", "blog_id": blogIdx};
      this.$axios.get(URL, {params: p}).then((suc) => {
        toast.clear();

        console.log(suc.data);
        that.details = suc.data.moments;
        console.log(that.details);
        // that.$el.querySelector(".cont").innerHTML=that.details.news_content;
      }).catch((err)=>{
          toast.clear();

          this.$toast.fail({
            message: "网络错误，请重试"
          });
        })
    }
  }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  .h2 {
    width: 80%;
    height: auto;
    padding-left: 10%;
    padding-right: 10%;
    margin: 0px;
    text-align: center;
    margin-top: 10px;
  }

  .msg {
    width: 100%;
    height: 25px;
    /*background: red;*/
    padding: 0 10%;
    box-sizing: border-box;
    margin-top: 10px;
  }

  .msg > p {
    width: 33%;
    height: 25px;
    float: left;
    font-size: 15px;
    color: gray;
    line-height: 25px;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }

  .time {
    text-align: center;
  }

  .view {
    text-align: end;
  }

  .blogdetails_cont {

    margin: 0 auto 0 auto;
    padding: 10px;
    padding-top: 0;
    box-sizing: border-box;
    /*border: 1px solid black;*/
    text-align: justify;
    width: 100%;
    height: auto;
  }

</style>



