<template>
  <div class="root" >

    <div style="height: 40px;">
      <div style="z-index: 999;height: 40px;position: fixed;width: 100%;border-bottom: 1px solid rgba(0,0,0,0.1);background: #0daf90;color: white;">
        <p style="margin: 0;height: 40px;line-height: 40px;text-align: center;font-size: 20px;font-family: fantasy">RRB Blog</p>
      </div>
    </div>
    <!--<van-nav-bar-->
      <!--title="注册"-->
    <!--/>-->

    <home v-if="active==0"></home>
    <rank v-if="active==1"></rank>
    <mine v-if="active==2"></mine>

    <van-tabbar v-model="active" active-color="#0daf90" inactive-color="#000" style="font-size: 20px">
      <van-tabbar-item icon="home-o" style="font-size: 15px">大厅</van-tabbar-item>
      <van-tabbar-item icon="bar-chart-o" style="font-size: 15px">排行榜</van-tabbar-item>
      <van-tabbar-item icon="friends-o" style="font-size: 15px">我的</van-tabbar-item>
    </van-tabbar>

  </div>
</template>

<script>
  import Home from "./home/Home"
  import Mine from "./mine/Mine"
  import Rank from "./rank/Rank"
    export default {
        name: "Root",
      components: {Home, Mine, Rank},
      data() {
        return {
          active: 0,
        }
      },
      // watch: {
      //   此处监听tabBar变化
      //   active(i) {
      //     console.log(i);
      //   }
      // }

      created() {
        //获取需要显示tab索引
        let tabIdx = this.$route.query.tabBarIndex;
        // console.log(tabIdx);
        if(tabIdx){
          if(typeof tabIdx != 'number'){ tabIdx = Number(tabIdx) }
          this.active = tabIdx;
        }
      }
    }
</script>

<style scoped>
>>>.van-tabbar-item__icon{
  /*width: 20px;*/
  /*height: 20px;*/
}
</style>



