<template>
  <div>
    <van-nav-bar
      title="个人信息"
      left-text="我的"
      left-arrow
      @click-left="onClickLeft"
    />
  </div>
</template>

<script>
    export default {
        name: "Userinfo",
      methods:{
        onClickLeft() {
          this.$router.push({
            path: '/',
            query: {
              tabBarIndex: 2
            }
          })
        },
      }
    }
</script>

<style scoped>

</style>



