import Vue from 'vue'
import Router from 'vue-router'
import Root from '@/components/Root'
import Mine from '@/components/mine/Mine'
import Home from '@/components/home/Home'
import Rank from '@/components/rank/Rank'
import Login from '@/components/mine/Login'
import Register from '@/components/mine/Register'
import Clockin from '@/components/mine/Clockin'
import Userinfo from "../components/mine/Userinfo";
import Mylist from "../components/mine/Mylist";
import Publish from "../components/mine/Publish";
import Myblogs from "../components/mine/Myblogs";
import Blogdetails from "../components/mine/Blogdetails";










Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'Root',
      component: Root
    },
    {
      path: '/blogdetails',
      name: 'Blogdetails',
      component: Blogdetails
    },
    {
      path: '/userinfo',
      name: 'Userinfo',
      component: Userinfo
    },
    {
      path: '/publish',
      name: 'Publish',
      component: Publish
    },
    {
      path: '/myblogs',
      name: 'Myblogs',
      component: Myblogs
    },
    {
      path: '/mylist',
      name: 'Mylist',
      component: Mylist
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/clockin',
      name: 'Clockin',
      component: Clockin
    },
    {
      path: '/mine',
      name: 'Mine',
      component: Mine
    },
    {
      path: '/rank',
      name: 'Rank',
      component: Rank
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    }
  ]
})



