
//设置cookie(name:String,value:String,expireDays:Number)
export function setCookie(cookieName,cookieValue,expiresDays) {
    if (cookieName&&cookieValue&&expiresDays){
        cookieName=cookieName.replace(/\s+/g,"");
        cookieValue=cookieValue.trim();
        if(cookieName && cookieValue){
            let d = new Date();
            d.setTime(d.getTime() + expiresDays * 24 * 60 * 60 * 1000);
            let gmtTimeStr = d.toUTCString();
            document.cookie = cookieName+"="+cookieValue+"; expires="+gmtTimeStr+"; path=/";
            console.log("setCookie success");
        }
    }
}

//查询cookie(name:String)
export function getCookie(cookieName) {
    cookieName+="=";
    let cookiesArr=document.cookie.split(";");
    for (let i = 0; i <cookiesArr.length ; i++) {
        let item=cookiesArr[i].trim();
        if (item.startsWith(cookieName)) {
            console.log("getCookie success");
            return item.substring(cookieName.length);
        }

    }
    return "";
}

//更改cookie(name:String,value:String,expireDays:Number)
export function changeCookie(cookieName,cookieValue,expiresDays) {
    if (cookieName&&cookieValue&&expiresDays){
        cookieName=cookieName.replace(/\s+/g,"");
        cookieValue=cookieValue.trim();
        let getCooVal= getCookie(cookieName);
        if(cookieName && cookieValue&&getCooVal){
            let d = new Date();
            d.setTime(d.getTime() + expiresDays * 24 * 60 * 60 * 1000);
            let gmtTimeStr = d.toUTCString();
            document.cookie = cookieName+"="+cookieValue+"; expires="+gmtTimeStr+"; path=/";
            console.log("changeCookie success");
        }else {
            alert("name不存在");
        }
    }
}

//删除cookie(name:String)
export function removeCookie(cookieName) {
    let gmtTimeStr=new Date().toUTCString();
    if(cookieName){
        document.cookie = cookieName+"=*; expires="+gmtTimeStr+"; path=/";
        console.log("removeCookie success");
    }else {
        alert("输入为空");
    }


}
//把日期时间转成字符串格式 并补0(x:Number);
export function addZeroToString(x) {
    x=x.toString();
    if (x.length<2){
        x="0"+x;
    }
    return x;
}



