<template>
  <div>
    <van-nav-bar
      title="登录"
      left-text="我"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form @submit="onSubmit">
      <van-field
        v-if="isIf"
        v-model="email"
        center
        clearable
        label="邮箱"
        placeholder="邮箱"
        :rules="[{ required: true, message: '请输入邮箱' }]"
      />
      <van-field
        v-if="isIf"
        v-model="password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true , message: '请输入密码' }]"
      />
      <van-cell center title="记住密码">
        <template #right-icon>
          <van-switch v-model="checked" size="24" />
        </template>
      </van-cell>

      <div style="margin: 16px;" v-if="isIf">
        <van-button round block type="info" native-type="submit">
          提交
        </van-button>
      </div>
    </van-form>
    <van-cell-group v-if="isIf">
      <van-cell title="没有账号？" value="去注册" to='/register'/>
    </van-cell-group>

  </div>
</template>

<script>
  export default {
    name: "Register",
    data() {
      return {
        email: '',
        password: '',
        isIf: true,
        checked: false,

      }
    },
    mounted() {

    },
    methods: {

      onClickLeft() {
        this.$router.push({
          path: '/',
          query: {
            tabBarIndex: 2
          }
        })
      },
      onSubmit() {
        const toast = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true,
          message: "正在登陆..."
        });
        let that = this;
        let params = {"key": "vv9544", "email": this.email, "password": this.password};
        let paramsStr = this.$qs.stringify(params);
        let loginURL = "https://zhuzuoji.com/myapi/employee/login";
        this.$axios.post(loginURL, paramsStr).then((suc) => {
          console.log(suc);
          if (!suc.data.status) {
            this.$toast.fail({
              message: suc.data.msg
            })
          } else {
            toast.clear();
            if(this.checked){
              that.$setCookie("avatar", suc.data.info.emp_avatar, 7);
              that.$setCookie("e_name", suc.data.info.emp_name, 7);
              that.$setCookie("e_email", suc.data.info.emp_email, 7);
              that.$setCookie("e_inte", suc.data.info.emp_inte.toString(), 7);
            }else {
              that.$setCookie("avatar", suc.data.info.emp_avatar, "*");
              that.$setCookie("e_name", suc.data.info.emp_name, "*");
              that.$setCookie("e_email", suc.data.info.emp_email, "*");
              that.$setCookie("e_inte", suc.data.info.emp_inte.toString(), "*");
            }

            this.$toast.success({
              message: suc.data.msg
            })

            setTimeout(function () {
              that.$router.push({
                path: '/',
                query: {
                  tabBarIndex: 2
                }
              })
            }, 2000)

          }
        }).catch((err) => {
          console.log(err);

          this.$toast.fail({
            message: err.data.msg

          })
        })
      }
    }
  }
</script>

<style scoped>

</style>



