// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import qs from 'qs'
import {setCookie,getCookie,changeCookie,removeCookie,addZeroToString} from "./utils/shioyuTools"
import VueQuillEditor from 'vue-quill-editor'
Vue.use(VueQuillEditor, /* { default global options } */)

// require styles
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import { Tabbar, TabbarItem } from 'vant';

import { Icon } from 'vant';
import { Cell, CellGroup } from 'vant';

import { Form } from 'vant';

import { Button }from 'vant';
import { Switch } from 'vant';
import { NavBar } from 'vant';
import { Toast } from 'vant';

import { Field } from 'vant';
import { Image as VanImage } from 'vant';

import { Uploader } from 'vant';
import { Card } from 'vant';
import { RadioGroup, Radio } from 'vant';
import { Checkbox, CheckboxGroup } from 'vant';
import { Tab, Tabs } from 'vant';
import { Loading } from 'vant';
import { PullRefresh } from 'vant';

Vue.use(PullRefresh);

Vue.use(Loading);

Vue.use(Tab);
Vue.use(Tabs);

Vue.use(Checkbox);
Vue.use(CheckboxGroup);

Vue.use(Radio);
Vue.use(RadioGroup);

Vue.use(Card);

Vue.use(Uploader);


Vue.use(Field);
Vue.use(Form);
Vue.use(Toast);
Vue.use(NavBar);
Vue.use(Switch);
Vue.use(Button);
Vue.use(Form);
Vue.use(VanImage);
Vue.use(NavBar);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Icon);
Vue.use(Tabbar);
Vue.use(TabbarItem);

Vue.config.productionTip = false;
Vue.prototype.$setCookie=setCookie;
Vue.prototype.$getCookie=getCookie;
Vue.prototype.$changeCookie=changeCookie;
Vue.prototype.$removeCookie=removeCookie;
Vue.prototype.addZeroToString=addZeroToString;
Vue.prototype.$axios = axios;
Vue.prototype.$qs = qs;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



