<template>
  <div>
    <van-nav-bar
      title="不定时打卡"
      left-text="我的"
      left-arrow
      @click-left="onClickLeft"
    />
    <textarea v-model="txt" class="t_area" placeholder="请输入想输入的东西吧"></textarea>

    <div class="up_load">

      <van-uploader v-model="fileList">
        <van-button type="default">上传文件</van-button>

        <template #preview-cover="{ file }">
          <div class="preview-cover van-ellipsis">{{ file.name }}</div>
        </template>
      </van-uploader>
    </div>


    <div style="margin: 6px;">
      <van-button block type="primary" native-type="button" @click="ClockinSubmit">确认发布</van-button>
    </div>


  </div>
</template>

<script>
  export default {
    name: "Clockin",
    data() {
      return {
        txt: "",
        fileList: [],
      };
    },
    methods: {
      onClickLeft() {
        this.$router.push({
          path: '/',
          query: {
            tabBarIndex: 2
          }
        })
      },
      // afterRead(file) {
      //   this.fileList.push(file);
      //   console.log(this.fileList)
      // },
      ClockinSubmit() {
        const toast = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true,
          message: "发布中..."
          // toast.clear();
        });
        let that = this;
        let us = this.$getCookie("e_email");
        let cont = this.$el.querySelector("textarea").value;
        let file = this.fileList[0].file;
        if (cont == "") {
          this.$toast.fail({
            message: '请输入内容'
          });

        } else if (this.fileList.length == 0) {
          this.$toast.fail({
            message: '请上产图片'
          });
        } else {
          let formData = new FormData;
          console.log(us, cont, file);
          formData.append("key", "vv9544");
          formData.append("email", us);
          formData.append("content", cont);
          formData.append("mtspic", file);
          let URL = "https://zhuzuoji.com/myapi/moments/addmoments";
          let config = {
            headers: {'content-type': 'multipart/form-data'}
          };
          this.$axios.post(URL, formData, config).then((suc) => {
            toast.clear();
            this.$toast.success({
              message: suc.data.msg+"积分+2"
            });
              that.$setCookie("e_inte",suc.data.new_inte.toString(),7);

            setTimeout(function () {
              that.$router.push({
                path: '/',
                query: {
                  tabBarIndex: 2
                }
              })
            },1500)
          }).catch((err)=>{
            toast.clear();
            this.$toast.fail({
              message: "网络错误，请重试"
            });
          })
        }
      }
    }
  }
</script>

<style scoped>
  .t_area {
    padding: 10px;
    resize: none;
    width: 100%;
    margin: 0 auto;
    height: 10rem;
    border: rgba(0, 0, 0, 0.1) 1px solid;
    box-sizing: border-box;
  }

  .preview-cover {
    position: absolute;
    box-sizing: border-box;
    bottom: 0;
    width: 100%;
    padding: 4px;
    color: #fff;
    font-size: 12px;
    text-align: center;
    background: rgba(0, 0, 0, 0.3);
  }
.up_load{
  width: 100%;
  /*background: red;*/
}
  .up_load > div {
    width: 100%;
    margin: 0 auto;
  }
  .van-uploader__preview{
    margin: 0 auto;
  }
>>>.van-uploader__input{
  width: 100%;
}
>>>.van-uploader__wrapper{
  text-align: center;
  width: 100%;
}
>>>.van-uploader__input-wrapper{
  width: 97%;
  margin: 0 auto;
}
>>>.van-uploader__input-wrapper>button{
  width: 100%;

}
  >>>.van-uploader__preview{
    margin-top:10px ;
    margin-left: 10px;
  }
  /*>>>.ql-editor{*/
    /*height: 300px;*/
  /*}*/

</style>



