<template>
  <div>
    <van-tabs v-model="active" swipeable animated>
      <van-tab title="积分榜">
        <div style="height:50px"></div>
        <van-pull-refresh
          v-model="isLoading"
          success-text="刷新成功"
          @refresh="onRefresh"
        >
        <div v-for="(list,index) in inte_list">
          <div class="mo">
            <div class="l">
              <p class="name">{{list.emp_name}}</p>
              <p class="no">NO.{{index+1}}</p>
            </div>
            <div class="r">
              <p class="jiaonang">{{list.emp_inte}}积分</p>
            </div>
          </div>
        </div>
          <div style="height:50px;border-top: 1px solid rgb(215,215,215)"></div>

        </van-pull-refresh>
      </van-tab>

      <van-tab title="打卡榜">
        <div style="height:50px"></div>

        <van-pull-refresh
          v-model="isLoading1"
          success-text="刷新成功"
          @refresh="onRefresh1"
        >
        <div v-for="(list,index) in moments_list">
          <div class="mo">
            <div class="l">
              <p class="name">{{list.emp_name}}</p>
              <p class="no">NO.{{index+1}}</p>
            </div>
            <div class="r">
              <p class="jiaonang">{{list.moments_count}}次</p>
            </div>
          </div>
        </div>
          <div style="height:50px;border-top: 1px solid rgb(215,215,215)"></div>

        </van-pull-refresh>
      </van-tab>
      <van-tab title="blog榜">
        <div style="height:50px"></div>

        <van-pull-refresh
          v-model="isLoading2"
          success-text="刷新成功"
          @refresh="onRefresh2"
        >
        <div v-for="(list,index) in blog_list">
          <div class="mo">
            <div class="l">
              <p class="name">{{list.emp_name}}</p>
              <p class="no">NO.{{index+1}}</p>
            </div>
            <div class="r">
              <p class="jiaonang">{{list.blog_count}}篇</p>
            </div>
          </div>
        </div>
          <div style="height:50px;border-top: 1px solid rgb(215,215,215)"></div>

        </van-pull-refresh>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
    export default {
        name: "rank",
      data() {
        return {
          active: 0,
          inte_list:[],
          moments_list:[],
          blog_list:[],
          isLoading:false,
          isLoading1:false,
          isLoading2:false,

        };
      },
      mounted() {
        const toast = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true,
          message: "加载中..."
          // toast.clear();

          // .catch((err)=>{
          //   toast.clear();
          //
          //   this.$toast.fail({
          //     message: "网络错误，请重试"
          //   });
          // })
        });

        let that=this;
        let URL="https://zhuzuoji.com/myapi/rank/getRankByInte";
        let p={'key':"vv9544"};
        this.$axios.get(URL,{params:p}).then((suc)=>{
          toast.clear();

          console.log(suc.data);
          that.inte_list=suc.data.rank_data;
        }).catch((err)=>{
              toast.clear();

              this.$toast.fail({
                message: "网络错误，请重试"
              });
            });

        let URL1="https://zhuzuoji.com/myapi/rank/getRankByMomentsCount";
        this.$axios.get(URL1,{params:p}).then((suc)=>{
          toast.clear();

          console.log(suc.data);
          that.moments_list=suc.data.rank_data;
        }).catch((err)=>{
              toast.clear();

              this.$toast.fail({
                message: "网络错误，请重试"
              });
            });

        let URL2="https://zhuzuoji.com/myapi/rank/getRankByBlogCount";
        this.$axios.get(URL2,{params:p}).then((suc)=>{
          console.log(suc.data);
          that.blog_list=suc.data.rank_data;
        }).catch((err)=>{
              toast.clear();

              this.$toast.fail({
                message: "网络错误，请重试"
              });
            });

        },
      methods:{
        onRefresh() {
          let that=this;
          let URL="https://zhuzuoji.com/myapi/rank/getRankByInte";
          let p={'key':"vv9544"};
          this.$axios.get(URL,{params:p}).then((suc)=>{
            console.log(suc.data);
            that.inte_list=suc.data.rank_data;
            this.$toast('刷新成功');
            this.isLoading = false;
          });
        },
        onRefresh1() {
          let that=this;
          let URL="https://zhuzuoji.com/myapi/rank/getRankByMomentsCount";


          let p={'key':"vv9544"};
          this.$axios.get(URL,{params:p}).then((suc)=>{
            console.log(suc.data);
            that.moments_list=suc.data.rank_data;
            this.$toast('刷新成功');
            this.isLoading1 = false;
          });
        },
        onRefresh2() {
          let that=this;
          let URL="https://zhuzuoji.com/myapi/rank/getRankByBlogCount";
          let p={'key':"vv9544"};
          this.$axios.get(URL,{params:p}).then((suc)=>{
            console.log(suc.data);
            that.blog_list=suc.data.rank_data;

            this.$toast('刷新成功');
            this.isLoading2 = false;
          });
        },
      }

    }
</script>

<style scoped>
  *{
    margin: 0;
    padding: 0;
  }
.mo{
  width: 100%;
  height: 70px;
  padding: 5px;
  /*background: greenyellow;*/
  border-top: 1px solid rgb(215,215,215);
}
  .l{
    width: 50%;
    /*background: green;*/
    height: 60px;
    float: left;
  }
  .name{
    text-align: center;
    width: auto;
    height: 20px;
    margin-top:15px ;
  }
  .no{
    text-align: center;
    width: auto;
    height: 20px;
  }
  .r{
    width: 50%;
    /*background: red;*/
    height: 50px;
    float: left;

    padding-top:25px ;
    box-sizing: border-box;
  }
  .jiaonang{
    width: 33%;
    margin: 0 auto;
    text-align: center;
    height: 20px;
    border-radius:10px ;
    background: rgb(210,210,210);
    color: rgb(200,133,86);
  }
  >>>.van-tabs__wrap{
    z-index: 999;
    position: fixed;
    width: 100%;
    top: 5.6%;
  }
  >>>.van-pull-refresh__track>div:nth-child(2){
    background: goldenrod;
  }
  >>>.van-pull-refresh__track>div:nth-child(3){
    background: silver;
  }
  >>>.van-pull-refresh__track>div:nth-child(4){
    background: tomato;
  }
</style>



