<template>
    <div>
      <van-nav-bar
        title="发布文章"
        left-text="我的"
        left-arrow
        @click-left="onClickLeft"
      />
      <van-cell-group style="border: 1px solid rgba(0,0,0,0.3)">

        <van-field
          v-model="title"
          placeholder="文章标题"
          maxlength="20"
          border
          show-word-limit
          size="large"/>
      </van-cell-group>
      <quill-editor ref="myTextEditor"
                    :content="content"
                    :options="editorOption"
                    @change="onEditorChange($event)"

      >
      </quill-editor>
      <div style="margin: 6px;">
        <van-button block type="primary" native-type="button" @click="PublishSubmit">确认发布</van-button>
      </div>
    </div>
</template>

<script>
  import { quillEditor } from 'vue-quill-editor'
    export default {
        name: "Publish",
      components: {quillEditor},
      data() {
        return {
          content: "",
          title:"",
          isFromMine:"true",

          editorOption: {
            placeholder: '在这里编辑博客正文...',

            modules:{toolbar:[
                ['bold', 'italic', 'underline', 'image'],
                [{ 'header': 1 }, { 'header': 2 }],
                [{ 'size': ['small', false, 'large', 'huge'] }],
                [{ 'color': [] }],
                [{ 'align': [] }],
                ['clean']
              ]}
          }

        }
      },
      methods: {
        onEditorChange({editor, html}) {
          this.content = html;
          console.log(html);
        },
        onClickLeft() {
          if(!this.isFromMine){
            this.$router.push({
              path: '/',
              query: {
                tabBarIndex: 2
              }
            })
          }else {
            this.$router.push({
              path: '/myblogs',

            })
          }

        },
        PublishSubmit() {
          const toast = this.$toast.loading({
            duration: 0, // 持续展示 toast
            forbidClick: true,
            message: "发布中..."
            // toast.clear();

            // this.$toast.fail({
            //   message: "网络错误，请重试"
            // });
          });
          let that = this;
          let name = this.$getCookie("e_name");
          let tit = this.title;
          let cont = this.content;
          let email = this.$getCookie("e_email");

          if (tit == "") {
            this.$toast.fail({
              message: '请输入标题'
            });

          } else if (cont=="") {
            this.$toast.fail({
              message: '请输入内容'
            });
          } else {
            // let formData = new FormData;
            // console.log(us, cont, file);
            // formData.append("key", "vv9544");
            // formData.append("email", us);
            // formData.append("content", cont);
            // formData.append("mtspic", file);
            let URL = "https://zhuzuoji.com/myapi/blog/addblog";
            // let config = {
            //   headers: {'content-type': 'multipart/form-data'}
            // };
            let params = {"key": "vv9544", "title": tit,"content":cont,"email":email,"nickname":name};

            this.$axios.post(URL, params).then((suc) => {
              toast.clear();
              console.log(suc.data);

              this.$toast.success({
                message: suc.data.msg+"积分+5"
              });
              that.$setCookie("e_inte",suc.data.new_inte.toString(),7);

              setTimeout(function () {

                that.$router.push({
                  path: '/',
                  query: {
                    tabBarIndex: 2
                  }
                })
              },1500)
            }).catch((err)=>{
              toast.clear();

              this.$toast.fail({

                message: "网络错误，请重试"
              });
            })
          }
        }

      },
      mounted() {
        this.isFromMine = this.$route.query.isFromMine;
      }
    }
</script>

<style scoped>
  .ql-editor{
    height:800px;
  }
  .ql-container{
    height: 800px;
  }


</style>



