<template>
  <div>
    <van-nav-bar
      title="我的文章"
      left-text="我的"
      left-arrow
      @click-left="onClickLeft"
    />
    <div>
      <!--<van-loading /> <van-loading type="spinner" />-->
      <van-cell-group
        style="padding: 5px;margin: 10px;"
      >
        <div v-for="(list,index) in bloglist"
             style="margin-bottom: 10px">
          <van-cell style="border: 1px solid rgba(0,0,0,0.3);border-radius: 5px"
                    :title="list.news_title"
                    is-link
                    @click="toBlogDetails(index)"/>
        </div>
        <van-cell style="border: 1px solid rgba(0,0,0,0.3);border-radius: 5px;height: 46px"
                  icon="plus"
                  title="再来一篇"

                  @click="toAddBlog"
                  is-link>

        </van-cell>
      </van-cell-group>
    </div>

  </div>
</template>

<script>
  export default {
    name: "Myblogs",
    data() {
      return {
        bloglist: ""
      }
    },
    methods: {
      onClickLeft() {

        this.$router.push({
          path: '/',
          query: {
            tabBarIndex: 2
          }
        })
      },
      toBlogDetails(idx) {

        let that = this;
        let id = this.bloglist[idx].news_id;
        this.$router.push({
          path: '/blogdetails',
          query: {
            blogid: id,
            isFromWhere:"mine"
          }
        })


        // let URL="https://zhuzuoji.com/myapi/blog/findblogbyblogid";
        // let p={'key':"vv9544","blog_id":id};
        // this.$axios.get(URL,{params:p}).then((suc)=>{
        //   console.log(suc.data);
        //   that.newslist=suc.data.moments;
        // })

      },
      toAddBlog(){
        this.$router.push({
          path: '/publish',
          query: {
            isFromMine: false
          }
        })
      }
    },
    mounted() {
      const toast = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true,
        message: "加载中..."
        // toast.clear();
      });
      let isFromWhere=this.$route.query.isFromWhere

      let that = this;
      let URL = "https://zhuzuoji.com/myapi/blog/findmyblog";
      // let email=this.$getCookie("e_email");
      let p = {'key': "vv9544", "email": this.$getCookie("e_email")};

      this.$axios.get(URL, {params: p}).then((suc) => {
        toast.clear();
        console.log(suc.data);
        that.bloglist = suc.data.moments;
      })

    }
  }
</script>

<style scoped>

</style>



