<template>
    <div>
      <van-nav-bar
        title="个人信息"
        left-text="我的"
        left-arrow
        @click-left="onClickLeft"
      />
      <!--<div v-for="(list,index) in newslist">-->
      <!--<van-card-->
        <!--:num="list.mts_time2"-->
        <!--:price="list.mts_user_name"-->
        <!--:title="list.mts_content"-->
        <!--:thumb="list.mts_pic"-->
      <!--/>-->
    <!--</div>-->
      <div v-for="(list,index) in newslist" style="margin-top: 20px;padding-left: 8px">
          <div class="mo_card">
            <div class="mo1">
              <p class="p1">{{list.mts_time2.slice(8,10)}}</p>
              <p class="p2">{{list.mts_time2.slice(5,7)}}月</p>
            </div>
            <div class="mo2">
              <van-image height="100%" fit="cover" :src="list.mts_pic" />
            </div>

            <!--<img class="mo2" :src="list.mts_pic"/>-->
            <div class="mo3">{{list.mts_content}}</div>
          </div>
      </div>

    </div>
</template>

<script>
    export default {
        name: "Mylist",
      data(){
        return{
          newslist:[],
        }
      },
      mounted() {
        const toast = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true,
          message: "加载中..."
          // toast.clear();

            // .catch((err)=>{
            //   toast.clear();
            //
            //   this.$toast.fail({
            //     message: "网络错误，请重试"
            //   });
            // })
        });
        let that=this;
        let URL="https://zhuzuoji.com/myapi/moments/findmymoments";
        let p={'key':"vv9544","email":this.$getCookie("e_email")};

        this.$axios.get(URL,{params:p}).then((suc)=>{
          toast.clear();

          console.log(suc.data);
          that.newslist=suc.data.moments;
        }).catch((err)=>{
          toast.clear();

          this.$toast.fail({
            message: "网络错误，请重试"
          });
        })
      },
      methods:{
        onClickLeft() {
          this.$router.push({
            path: '/',
            query: {
              tabBarIndex: 2
            }
          })
        },
      }
    }
</script>

<style scoped>
.mo_card{
  width: 100%;
  height: 90px;
  /*background: red;*/
  margin-bottom: 10px;
  box-shadow: 0 10px 75px 0 rgba(0,0,0,.05);
}
  .mo1{
    float: left;
    width: 19%;
    height: 90px;
    /*background: greenyellow;*/
  }
  .p1{
    float: left;
    width: 56%;
    font-size: 30px;
    line-height: 1px;
  }
  .p2{
    line-height: 50px;
    float: left;
    width: 44%;
    font-size: 12px;
  }
  .mo2{
    width: 28%;
    float: left;
    height: 100%;
  }
  .mo3{
    width: 53%;
    float: left;
    height: 100%;
    font-size: 15px;
    overflow:ellipsis;
    padding-left: 5px;
    box-sizing: border-box;
    padding-top: 5px;
  }
</style>



