<template>
  <div id="app">
    <!--<img src="./assets/logo.png">-->
    <router-view v-if="isRouterAlive"/>
    <!---->

  </div>
</template>

<script>

export default {
  name: 'App',
  provide () {    //父组件中通过provide来提供变量，在子组件中通过inject来注入变量。
    return {
      reload: this.reload
    }
  },
  data() {
    return{
      isRouterAlive: true                    //控制视图是否显示的变量
    }
  },
  methods: {
    reload () {
      this.isRouterAlive = false;            //先关闭，
      this.$nextTick(function () {
        this.isRouterAlive = true;         //再打开
      })
    }
  },
}
</script>

<style>
  element.style {
    z-index: 1;
    height: 2rem;
  }
  .van-hairline--top-bottom::after {
    border-width: 0.013333rem 0;
  }
  [class*=van-hairline]::after {
    content: ' ';
    position: absolute;
    pointer-events: none;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    top: -50%;
    left: -50%;
    right: -50%;
    bottom: -50%;
    -webkit-transform: scale(.5);
    transform: scale(.5);
    border: 0 solid #eee;
  }
  [class*=van-hairline] {
    position: relative;
  }
  .van-tabbar--fixed {
    left: 0;
    bottom: 0;
    position: fixed;
    font-size: 0.4rem;
  }
  .van-tabbar {
    width: 100%;
    height: 2.5rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background-color: #fff;
  }

  .van-icon-wap-home::before {
    content: "\F05E";
    font-size: 0.5rem;
  }
  .van-icon::before {
    display: inline-block;
  }
  .van-tabbar-item {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    line-height: 1.2;
    font-size: 0.16rem;
    color: #666;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
  .van-tabbar-item--active {
    color: #1989fa;
  }
  .van-tabbar-item__icon {
    font-size: 0.24rem;
    position: relative;
    margin-bottom: 0.066667rem;
  }
  .van-icon-shop::before {
    content: "\F053";
    font-size: 0.5rem;
  }
  .van-icon::before {
    display: inline-block;
  }
  .ql-editor{
    height:200px;
  }
  .blogdetails_cont img{
    width: 100%;
  }
</style>



