<template>
  <div>

    <!--<van-cell-group>-->
      <!--<van-cell title="单元格" icon="" is-link to="/login">-->
        <!--&lt;!&ndash; 使用 right-icon 插槽来自定义右侧图标 &ndash;&gt;-->
        <!--<template #title>-->
          <!--<van-image-->
            <!--fit="cover"-->
            <!--round-->
            <!--width="5rem"-->
            <!--height="5rem"-->
            <!--:src="avatar_src"-->
          <!--/>-->
          <!--<span class="sp">{{emp_name}}</span>-->
          <!--<span class="jf">积分：{{e_inte}}</span>-->
        <!--</template>-->
      <!--</van-cell>-->
    <!--</van-cell-group>-->

<div class="user_show" @click="toLogin">
  <div class="img_postion">
    <van-image
      fit="cover"
      round
      width="5rem"
      height="5rem"
      :src="avatar_src"

    />
  </div>

  <span class="sp">{{emp_name}}</span>
  <span class="jf">积分：{{emp_inte}}</span>
</div>

    <van-cell-group>
      <van-cell title="" label="deep dark fantasy" />
      <van-cell title="每日打卡" icon="sign" is-link @click="onToClockin" />
      <van-cell title="打卡记录" icon="underway-o" is-link  @click="onToMylist"/>
      <van-cell title="文章发布" icon="edit" is-link @click="onToPublish"/>
      <van-cell title="我的文章" icon="description" is-link  @click="onToMyblogs"/>
    </van-cell-group>
    <div style="margin: 16px;" v-if="isIf" @click="onLogout">
      <van-button round block type="danger" native-type="button">
        退出登录
      </van-button>
    </div>

  </div>
</template>

<script>

  export default {
    inject:['reload'],
    name: "Mine",
    data(){
      return{
        avatar_src:"https://img.yzcdn.cn/vant/cat.jpeg",
        isLogin:true,
        emp_name:"未登录",
        isIf:false,
        emp_inte:"--",
      }
    },
    mounted() {

      if(this.$getCookie("e_name")){
        this.isLogin=true;
        this.avatar_src=this.$getCookie("avatar");
        this.emp_name=this.$getCookie("e_name");
        this.emp_inte=this.$getCookie("e_inte");
        this.isIf=true
      }


    },
    methods:{
      onLogout(){
        this.$removeCookie("e_name");
        this.$toast.success({
          message:'注销成功'
        });
        this.reload();
      },
      toLogin(){
        if(this.$getCookie("e_name")){
          this.$router.push("/Userinfo")
        }else {
          this.$router.push("/login")
        }
      },
      noLoginToast(logined,nologin){
        if(nologin){
          if(this.$getCookie("e_name")){
            this.$router.push(logined)
          }else {
            this.$router.push(nologin)
          }
        }else {
          if(this.$getCookie("e_name")){
            this.$router.push(logined)
          }else {
            this.$toast.fail({
              message:'请先登录'
            });
          }
        }

      },
      onToClockin(){
          this.noLoginToast("/Clockin","");
      },
      onToMylist(){
        this.noLoginToast("/Mylist","");
      },
      onToPublish(){
        this.noLoginToast("/Publish","");
      },
      onToMyblogs(){

          if(this.$getCookie("e_name")){
            this.$router.push({
              path: '/Myblogs',
              query: {
                isFromWhere:"mine"
              }
            })
          }else {
            this.$toast.fail({
              message:'请先登录'
            });
          }


        // this.noLoginToast("/Myblogs","");
        // this.$router.push({
        //   path: '/Myblogs',
        //   query: {
        //     isFromWhere:"mine"
        //   }
        // })
      },
    }
  }
</script>

<style scoped>
  .t {
    text-align: start;
  }

  .sp {
    position: absolute;
    top: 6rem;
    margin-left: 7rem;
    font-size: 20px;
  }
  .jf{
    position: absolute;
    top: 7.5rem;
    margin-left: 7rem;
    font-size: 12px;
  }
  .user_show{
    background:#0daf90 ;
    border-top:1px solid #0daf90 ;
    height: auto;
  }
  .img_postion{
    margin-top: 1.5rem;
    margin-left: 1rem;
    padding-bottom: 1rem;

  }
  .img_postion>div{
    border: white 1px solid;
  }
</style>



